    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style-type: none;
}

html, 
body {
    height: 100%;
    width: 100%;
}

/* Body Section */

body {
    background-color: var(--background-color);
    font-family: 'Open Sans';
    font-size: 62.5%;
}

/* Container Section */

.container {
    min-height: 100vh;
    display: grid;
    grid-template-columns: 75px repeat(10, 1fr);
    grid-template-areas: 
        "nav main main main main main main main main main main main";
}

/* Nav Section */

nav {
    grid-area: nav;
}

#nav_menu {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding: 2rem 0 2rem 0;
}

.nav_menu_list {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: .75rem;
}

.nav_menu_items {
    font-size: 1.5rem;
}

li:hover {
    cursor: pointer;
}

/* Main Section */

main {
    min-height: inherit;
    grid-area: main;
    display: grid;
    grid-template-columns: 1fr 2fr 2fr;
}

/* Section Element */

section {
    border: 1px solid none;
}

.todo_section_catergories {
    display: flex;
    flex-direction: column;
    gap: 2rem;
}

.todo_categories_container {
    padding: 2rem 0 2rem 0;
    display: flex;
    flex-direction: column;
    gap: 2rem;
}

.todo_list_menu {
    padding: 0 2rem 0 2rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.list_header {
    font-size: 1rem;
    font-weight: 500;
    text-transform: capitalize;
}

.list_items {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 1.25rem;
}

.list_items i {
    font-size: 1.5rem;
}

/* Header Section */

#main_section {
    padding: 0 2rem 0 2rem;
}

.header {
    display: flex;
    flex-direction: column;
    padding: 1.75rem 0 2rem 0;
    gap: 1rem;
}

.header_container {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.main_section_container {
    display: flex;
    align-items: center;
    gap: .25rem;
}

.main_section_container i {
    font-size: 1.75rem;
}

#header_title {
    font-size: 1.75rem;
    font-weight: 500;
}

#header_menu_list {
    display: flex;
    gap: 1rem;
}

.header_list_items {
    font-size: 1.5rem;
}

.second_item_header {
    transform: rotate(90deg);
}

.header_search_container {
    display: block;
}

.search_input {
    border: none;
    height: 1.75rem;
    width: 100%;
}

.search_input:focus {
    outline: 1px solid red;
}

.search_container {
    border: 1px solid #000;
    display: flex;
    align-items: center;
}

.search_container:focus {
    outline: none;
}

.search_display {
    display: none;
    font-size: 1.5rem;
}

input[type=text] {
    font-size: 1rem;
    font-weight: 400;
}

input::placeholder {
    font-family: 'Karla';
    font-size: 1.25rem;
}

/* Todo Details Header */

.todo_details_header {
    display: flex;
    justify-content: space-between;
    padding: 2rem 2rem 2rem 2rem;
}

.todo_input_container {
    display: flex;
    gap: .5rem;
}

input[type=checkbox] {
    border-radius: 100rem;
}

 .todo_details {
    font-size: 1.25rem;
    font-weight: 400;
 }`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAEA;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;IACtB,qBAAqB;AACzB;;AAEA;;IAEI,YAAY;IACZ,WAAW;AACf;;AAEA,iBAAiB;;AAEjB;IACI,yCAAyC;IACzC,wBAAwB;IACxB,gBAAgB;AACpB;;AAEA,sBAAsB;;AAEtB;IACI,iBAAiB;IACjB,aAAa;IACb,2CAA2C;IAC3C;oEACgE;AACpE;;AAEA,gBAAgB;;AAEhB;IACI,cAAc;AAClB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,8BAA8B;IAC9B,sBAAsB;AAC1B;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,WAAW;AACf;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,eAAe;AACnB;;AAEA,iBAAiB;;AAEjB;IACI,mBAAmB;IACnB,eAAe;IACf,aAAa;IACb,kCAAkC;AACtC;;AAEA,oBAAoB;;AAEpB;IACI,sBAAsB;AAC1B;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,sBAAsB;IACtB,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,sBAAsB;IACtB,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,0BAA0B;AAC9B;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,8BAA8B;IAC9B,kBAAkB;AACtB;;AAEA;IACI,iBAAiB;AACrB;;AAEA,mBAAmB;;AAEnB;IACI,sBAAsB;AAC1B;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,yBAAyB;IACzB,SAAS;AACb;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,8BAA8B;AAClC;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,WAAW;AACf;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,kBAAkB;IAClB,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,SAAS;AACb;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,wBAAwB;AAC5B;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,YAAY;IACZ,eAAe;IACf,WAAW;AACf;;AAEA;IACI,sBAAsB;AAC1B;;AAEA;IACI,sBAAsB;IACtB,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,iBAAiB;AACrB;;AAEA;IACI,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,oBAAoB;IACpB,kBAAkB;AACtB;;AAEA,wBAAwB;;AAExB;IACI,aAAa;IACb,8BAA8B;IAC9B,4BAA4B;AAChC;;AAEA;IACI,aAAa;IACb,UAAU;AACd;;AAEA;IACI,qBAAqB;AACzB;;CAEC;IACG,kBAAkB;IAClB,gBAAgB;CACnB","sourcesContent":["@import url('https://fonts.googleapis.com/css2?family=Fira+Code:wght@300..700&family=Karla:ital,wght@0,200..800;1,200..800&family=Matangi:wght@300..900&family=Open+Sans:ital,wght@0,300..800;1,300..800&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,70